import { useState, useEffect } from "react";
import "./PaperClub.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTelegram, faXTwitter } from "@fortawesome/free-brands-svg-icons";

import bg1 from "../Images/1.jpg";
import bg2 from "../Images/2.jpg";
import bg3 from "../Images/3.jpg";
import bg4 from "../Images/4.jpg";
import bg5 from "../Images/5.jpg";
import bg6 from "../Images/6.jpg";
import bg7 from "../Images/7.jpg";

import logo from "../Images/logo.jpg";

import signature from "../Images/signature.webp";


let noteId = 0;

export default function PaperClub() {
  const backgrounds = [bg1, bg2, bg3, bg4, bg5, bg6, bg7];
  const [currentBg, setCurrentBg] = useState(0);
  const [prevBg, setPrevBg] = useState(null);
  const [isFading, setIsFading] = useState(false);
  
  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    backgrounds.forEach((src) => {
      const img = new Image();
      img.src = src;
    });
  }, [backgrounds]);

  useEffect(() => {
    const interval = setInterval(() => {
      setPrevBg(currentBg);
      setCurrentBg((i) => (i + 1) % backgrounds.length);
      setIsFading(true);
      setTimeout(() => setIsFading(false), 1000);
    }, 5000);
    return () => clearInterval(interval);
  }, [currentBg, backgrounds.length]);

  const [count, setCount] = useState(0);
  const [animatedNotes, setAnimatedNotes] = useState([]);
  const [userCountryCode, setUserCountryCode] = useState("Unknown");
  const [userCountryName, setUserCountryName] = useState("Unknown");
  const [countryPops, setCountryPops] = useState({});
  const [showLeaderboard, setShowLeaderboard] = useState(false);

  useEffect(() => {
    fetch("https://ipapi.co/json")
      .then((res) => res.json())
      .then((data) => {
        setUserCountryCode(data.country || "Unknown");
        setUserCountryName(data.country_name || "Unknown");
      })
      .catch(() => {
        setUserCountryCode("Unknown");
        setUserCountryName("Unknown");
      });
  }, []);

  const countryCodeToFlagEmoji = (code) => {
    if (!code || code.length !== 2) return "❓";
    return code
      .toUpperCase()
      .replace(/./g, (char) =>
        String.fromCodePoint(127397 + char.charCodeAt())
      );
  };

  const API_BASE = "https://paper-production-c484.up.railway.app";

const handleCountClick = () => {
  const newNote = { id: noteId++, value: count };
  setAnimatedNotes((notes) => [...notes, newNote]);
  setCount((c) => c + 1);

  // Increment pop count in backend
  fetch(`${API_BASE}/click`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ country: userCountryCode }),
  });

  setTimeout(() => {
    setAnimatedNotes((notes) =>
      notes.filter((n) => n.id !== newNote.id)
    );
  }, 800);
};


  const [noteConfigs] = useState(() =>
    Array.from({ length: 30 }, () => ({
      left: Math.random() * 100,
      duration: 4 + Math.random() * 6,
      delay: Math.random() * 4,
    }))
  );

  const [copied, setCopied] = useState(false);
  const fullAddress = "b37NMA81bSgVmC6E5TXyrUndRzwdYycxoLFsG3gpump";

const isMobile = typeof window !== "undefined" && window.innerWidth < 768;
  const [shortenedAddress, setShortenedAddress] = useState("");

    useEffect(() => {
    const updateAddress = () => {
      const isMobile = window.innerWidth < 768;
      const start = isMobile ? 1 : 6;
      const end = isMobile ? 1 : 6;
      setShortenedAddress(fullAddress.slice(0, start) + ".." + fullAddress.slice(-end));
    };

    updateAddress(); // on mount
    window.addEventListener("resize", updateAddress);
    return () => window.removeEventListener("resize", updateAddress);
  }, [fullAddress]);


const handleCopy = () => {
  if (navigator.clipboard && navigator.clipboard.writeText) {
    navigator.clipboard.writeText(fullAddress).then(() => {
      setCopied(true);
      setTimeout(() => setCopied(false), 1000);
    }).catch(() => fallbackCopy());
  } else {
    fallbackCopy();
  }
};

const fallbackCopy = () => {
  const textArea = document.createElement("textarea");
  textArea.value = fullAddress;
  document.body.appendChild(textArea);
  textArea.focus();
  textArea.select();

  try {
    const successful = document.execCommand("copy");
    if (successful) {
      setCopied(true);
      setTimeout(() => setCopied(false), 1000);
    } else {
      alert("Copy failed");
    }
  } catch (err) {
    alert("Copy not supported");
  }

  document.body.removeChild(textArea);
};


  const leaderboardData = Object.entries(countryPops)
    .map(([countryCode, pops]) => ({
      countryCode,
      pops,
      flag: countryCodeToFlagEmoji(countryCode),
    }))
    .sort((a, b) => b.pops - a.pops);


useEffect(() => {
  const fetchLeaderboard = () => {
    fetch(`${API_BASE}/leaderboard`)
      .then((res) => res.json())
      .then((data) => {
        const pops = {};
        let total = 0;
        data.forEach(({ country, pops: count }) => {
          pops[country] = count;
          total += count;
        });
        setCountryPops(pops);
        setTotalPaper(total);
      });
  };

  fetchLeaderboard();
  const interval = setInterval(fetchLeaderboard, 5000); // Optional polling
  return () => clearInterval(interval);
}, []);


const [totalPaper, setTotalPaper] = useState(0);


  return (
    <div className="paperclub" onClick={handleCountClick}>


      {/* Header */}
   <div className="header-bar">

<div className="first-section">

       <a href="#/" onClick={(e) => e.stopPropagation()}>

  <img src={logo} alt="logo" className="logoimage" />
</a>
        <div className="contract-box">
   
          <span className="contract-icon" />
    <span className="contract-text" onClick={(e) => {
      e.stopPropagation();
      setShowModal(true);
    }}>
      {copied ? "Copied!" : `CA: ${shortenedAddress}`}
    </span>
        </div>
</div>


<div className="second-section">
        <div className="about">
          <button className="about-button" onClick={(e) => { e.stopPropagation(); window.location.href = "#/about"; }}>
            About
          </button>
        </div>

              <button className="raid-button" onClick={(e) => { e.stopPropagation(); window.location.href = "#/raid"; }}>
            Raid
          </button>


<div className="header-icons">
  <a
    href="https://x.com/i/communities/1931456049237864952"
    target="_blank"
    rel="noopener noreferrer"
    className="icon-twitter"
    aria-label="Twitter"
  >
    <FontAwesomeIcon icon={faXTwitter} size="lg" />
  </a>

  <a
    href="https://t.me/paperonsolana_million"
    target="_blank"
    rel="noopener noreferrer"
    className="icon-telegram"
    aria-label="Telegram"
  >
    <FontAwesomeIcon icon={faTelegram} size="lg" />
  </a>
</div>



        </div>


 </div>

     

      {/* Backgrounds */}
      {prevBg !== null && (
        <div
          className={`bg-layer ${isFading ? "fade-out" : ""}`}
          style={{ backgroundImage: `url(${backgrounds[prevBg]})` }}
        />
      )}
      <div
        className="bg-layer"
        style={{ backgroundImage: `url(${backgrounds[currentBg]})` }}
      />

      {/* Main sticky note */}
<div className="sticky-note">
  <div className="notebox">
    <div className="note-count">{count.toLocaleString()}$</div>
    <div className="note-text">
      This piece of paper could be worth 500 million dollars if we all
      agreed on it.
    </div>
    <img src={signature} alt="signature" className="signature-image" />
  </div>
</div>


      {/* Flying peeled notes */}
{animatedNotes.map((note) => (
  <div key={note.id} className="peeled-note">
    <div className="notebox">
      <div className="note-count">{note.value.toLocaleString()}$</div>
    </div>
  </div>
))}


      {/* Falling notes */}
      {noteConfigs.map((cfg, i) => (
        <div
          key={i}
          className="falling-note"
          style={{
            left: `${cfg.left}%`,
            animationDuration: `${cfg.duration}s`,
            animationDelay: `${cfg.delay}s`,
          }}
        >
          500,000,000
        </div>
      ))}

      {/* Leaderboard */}
      <div
        className={`leaderboard-container ${
          showLeaderboard ? "expanded" : ""
        }`}
      >
        <div
          className="leaderboard-header"
          onClick={() => setShowLeaderboard(!showLeaderboard)}
        >
          <span>TOTAL $paper</span>
          <span>{totalPaper.toLocaleString()}$</span>

          <span className="leaderboard-arrow">
            {showLeaderboard ? "▲" : "▼"}
          </span>
        </div>

        {showLeaderboard && (
          <div className="leaderboard-panel">
            <div className="leaderboard-title">LEADERBOARD</div>
            <div className="leaderboard-list">
              {leaderboardData.map((item, index) => (
                <div key={item.countryCode} className="leaderboard-row">
<span className="medal">
  {index === 0
    ? "🥇"
    : index === 1
    ? "🥈"
    : index === 2
    ? "🥉"
    : `${index + 1}`}
</span>

        
                 <span className="flag desktop-hide">{item.flag}</span>
  <span className="country mobile-hide">{item.countryCode}</span>
  <span className="pops">{item.pops.toLocaleString()}</span>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>

            {showModal && (
        <div className="modal-overlay" onClick={() => setShowModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <button className="modal-close" onClick={() => setShowModal(false)}>X</button>
            <h2>Token Contract Address</h2>
            <p>{fullAddress}</p>
            <button onClick={handleCopy}>{copied ? "Copied!" : "Copy to Clipboard"}</button>
            <button onClick={() => window.open(`https://solscan.io/token/${fullAddress}`, "_blank")}>View on Solscan</button>
          </div>
        </div>
      )}


    </div>
  );
}
