import React, { useState } from "react";
import "./about.css";
import logo from "../Images/logo.jpg";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTelegram, faXTwitter } from "@fortawesome/free-brands-svg-icons";

import background from "../Images/4.jpg";


import vid1 from "../Images/artwork/vid1.mp4";
import vid2 from "../Images/artwork/vid2.mp4";
import vid3 from "../Images/artwork/vid3.mp4";


import dex from "../Images/Icons/dex.png";
import bird from "../Images/Icons/bird.jpeg";
import gecko from "../Images/Icons/gecko.png";

export default function About() {
  const [copied, setCopied] = useState(false);
  const [showModal, setShowModal] = useState(false);

  const fullAddress = "b37NMA81bSgVmC6E5TXyrUndRzwdYycxoLFsG3gpump";
  const shortenedAddress =
    fullAddress.slice(0, 1) + ".." + fullAddress.slice(-1);


const handleCopy = () => {
  if (navigator.clipboard && navigator.clipboard.writeText) {
    navigator.clipboard.writeText(fullAddress).then(() => {
      setCopied(true);
      setTimeout(() => setCopied(false), 1000);
    }).catch(() => fallbackCopy());
  } else {
    fallbackCopy();
  }
};

const fallbackCopy = () => {
  const textArea = document.createElement("textarea");
  textArea.value = fullAddress;
  document.body.appendChild(textArea);
  textArea.focus();
  textArea.select();

  try {
    const successful = document.execCommand("copy");
    if (successful) {
      setCopied(true);
      setTimeout(() => setCopied(false), 1000);
    } else {
      alert("Copy failed");
    }
  } catch (err) {
    alert("Copy not supported");
  }

  document.body.removeChild(textArea);
};



const [activeImage, setActiveImage] = useState(null);


  const artworkImages = Array.from({ length: 24 }, (_, i) =>
  require(`../Images/artwork/${i + 1}.jpg`)
);



  return (
<div
  className="paperclub-about"
  style={{
    backgroundImage: `url(${background})`,
    backgroundSize: "cover",
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    minHeight: "100vh"
  }}
>

      {/* Header */}
      <div className="header-bar">
        <div className="first-section">
          <a href="#/" onClick={(e) => e.stopPropagation()}>

            <img src={logo} alt="logo" className="logoimage" />
          </a>
          <div className="contract-box">
            <span className="contract-icon" />
            <span
              className="contract-text"
              onClick={(e) => {
                e.stopPropagation();
                setShowModal(true);
              }}
            >
              {copied ? "Copied!" : `CA: ${shortenedAddress}`}
            </span>
          </div>
        </div>

        <div className="second-section">
          <div className="about">
            <button
              className="about-button"
              onClick={(e) => {
                e.stopPropagation();
              }}
            >
              About
            </button>
          </div>
              <button className="raid-button" onClick={(e) => { e.stopPropagation(); window.location.href = "#/raid"; }}>
            Raid
          </button>


<div className="header-icons">
  <a
    href="https://x.com/i/communities/1931456049237864952"
    target="_blank"
    rel="noopener noreferrer"
    className="icon-twitter"
    aria-label="Twitter"
  >
    <FontAwesomeIcon icon={faXTwitter} size="lg" />
  </a>

  <a
    href="https://t.me/paperonsolana_million"
    target="_blank"
    rel="noopener noreferrer"
    className="icon-telegram"
    aria-label="Telegram"
  >
    <FontAwesomeIcon icon={faTelegram} size="lg" />
  </a>
</div>



        </div>
      </div>



<div className="partners">
  <a
    href="https://x.com/i/communities/1931456049237864952"
    target="_blank"
    rel="noopener noreferrer"
    className="icon-link"
  >
    <FontAwesomeIcon icon={faXTwitter} className="icon" />
  </a>

  <a
    href="https://t.me/paperonsolana_million"
    target="_blank"
    rel="noopener noreferrer"
    className="icon-link"
  >
    <FontAwesomeIcon icon={faTelegram} className="icon" />
  </a>


  <a
    href="https://dexscreener.com/solana/b37NMA81bSgVmC6E5TXyrUndRzwdYycxoLFsG3gpump"
    target="_blank"
    rel="noopener noreferrer"
    className="icon-link"
  >
    <img src={dex} alt="Dexscreener" className="icon" />
  </a>

  <a
    href="https://birdeye.so/token/b37NMA81bSgVmC6E5TXyrUndRzwdYycxoLFsG3gpump?chain=solana"
    target="_blank"
    rel="noopener noreferrer"
    className="icon-link"
  >
    <img src={bird} alt="Birdeye.so" className="icon" />
  </a>

  <a
    href="https://www.coingecko.com/en/coins/500m-piece-of-paper"
    target="_blank"
    rel="noopener noreferrer"
    className="icon-link"
  >
    <img src={gecko} alt="CoinGecko" className="icon" />
  </a>
</div>





      {/* About content */}
<div className="about-page">
  <h1>What is $PAPER?</h1>
  <p>
    $PAPER is a collective movement — a meme disguised as an experiment in digital value.
    No utility. No promises. No whitepaper. Just a community clicking its way to imaginary riches.
  </p>
  <p>
    Every tap adds value to the same mythical note. It’s not backed by gold, tech, or logic —
    just belief. And maybe that's enough.
  </p>
  <p>
    We’re not here to change the world. We’re here to laugh, to click, and to see how far 
    nonsense can go when thousands believe it matters.
  </p>
  <p>
    Built by degens, fueled by dopamine, and held together by pure copium.
  </p>
  <p>
    No presale. No VC. No roadmap. Just paper and people.
  </p>



</div>


<div className="partners">

</div>



<div className="gallery-section">
  <div className="gallerycontainer">
  <h2 className="gallery-title">🖼️ Wall of Fame</h2>

<div className="video-grid">
  {[vid1, vid2, vid3].map((videoSrc, i) => (
    <div className="video-wrapper" key={i}>
      <video
        controls
        preload="metadata"
        width="100%"
        height="100%"
        muted
        loop
        playsInline
        style={{ borderRadius: "12px" }}
      >
        <source src={videoSrc} type="video/mp4" />
        Your browser does not support the video tag.
      </video>
    </div>
  ))}
</div>


  <div className="gallery">
    {artworkImages.map((src, index) => (
      <div key={index} className="gallery-item" onClick={() => setActiveImage(src)}>
        <img src={src} alt={`Artwork ${index + 1}`} />
      </div>
    ))}
  </div>

  {activeImage && (
    <div className="lightbox" onClick={() => setActiveImage(null)}>
      <img src={activeImage} alt="Fullscreen artwork" className="lightbox-image" />
    </div>
  )}
</div>
</div>





<div className="disclaimerbox">
  <div className="legal-disclaimer">
    <strong>Legal Disclaimer:</strong> $PAPER is a meme coin with no intrinsic value or expectation of financial return.
    $PAPER is for entertainment purposes only. When you purchase $PAPER, you are agreeing that you have seen this disclaimer.
  </div>
</div>

      {/* Modal */}
      {showModal && (
        <div className="modal-overlay" onClick={() => setShowModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <button className="modal-close" onClick={() => setShowModal(false)}>X</button>
            <h2>Token Contract Address</h2>
            <p>{fullAddress}</p>
            <button onClick={handleCopy}>{copied ? "Copied!" : "Copy to Clipboard"}</button>
            <button onClick={() => window.open(`https://solscan.io/token/${fullAddress}`, "_blank")}>View on Solscan</button>
          </div>
        </div>
      )}
    </div>
  );
}
