import React, { useState, useEffect } from "react";
import "./raid.css";
import raidbackground from "../Images/3.jpg";
import logo from "../Images/logo.jpg";


export default function Raid() {
  const [raids, setRaids] = useState([]);
  const [url, setUrl] = useState("");
  const [title, setTitle] = useState("");
  const [copiedShare, setCopiedShare] = useState(false);
  const [showForm, setShowForm] = useState(false);

  const API_BASE = "https://raids-production.up.railway.app/api";

  useEffect(() => {
    fetch(`${API_BASE}/raids`)
      .then((res) => res.json())
      .then((data) => {
        if (Array.isArray(data)) {
          setRaids(data);
        } else {
          console.error("Invalid data received:", data);
          setRaids([]);
        }
      })
      .catch(console.error);
  }, []);

  const isValidTweetUrl = (inputUrl) => {
    try {
      const parsed = new URL(inputUrl);
      return parsed.hostname === "twitter.com" || parsed.hostname === "x.com";
    } catch {
      return false;
    }
  };

  const isDuplicateUrl = (inputUrl) => {
    return raids.some((r) => r.url === inputUrl);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!url || !isValidTweetUrl(url)) {
      alert("Please enter a valid Tweet URL.");
      return;
    }
    if (isDuplicateUrl(url)) {
      alert("This link has already been submitted.");
      return;
    }
    fetch(`${API_BASE}/raids`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ url, title }),
    })
      .then((res) => res.json())
      .then((newRaid) => setRaids((prev) => [newRaid, ...prev]))
      .catch(console.error);

    setUrl("");
    setTitle("");
  };

  const handleBoost = (id) => {
    fetch(`${API_BASE}/boost/${id}`, {
      method: "POST",
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.error) {
          alert(data.error);
        } else {
          setRaids((prev) =>
            prev.map((r) => (r.id === id ? { ...r, boosts: data.boosts } : r))
          );
        }
      })
      .catch(console.error);
  };

  const formatTimeAgo = (timestamp) => {
    const seconds = Math.floor((Date.now() - timestamp) / 1000);
    if (seconds < 60) return `${seconds}s ago`;
    const minutes = Math.floor(seconds / 60);
    if (minutes < 60) return `${minutes}m ago`;
    const hours = Math.floor(minutes / 60);
    if (hours < 24) return `${hours}h ago`;
    const days = Math.floor(hours / 24);
    return `${days}d ago`;
  };

  const getTweetId = (url) => {
    const match = url.match(/status\/(\d+)/);
    return match ? match[1] : null;
  };

  const handleShare = (setCopied) => {
    const message = "🧻 RAID TIME: $PAPER is printing\nhttps://500mpapersolana.xyz/#/raid";

    try {
      navigator.clipboard.writeText(message).then(() => {
        setCopied(true);
        setTimeout(() => {
          setCopied(false);
          window.open("https://x.com/i/communities/1931456049237864952", "_blank");
        }, 1500);
      });
    } catch (err) {
      alert("Clipboard not supported.");
      window.open("https://x.com/i/communities/1931456049237864952", "_blank");
    }
  };


    const [noteConfigs] = useState(() =>
    Array.from({ length: 30 }, () => ({
      left: Math.random() * 300,
      duration: 4 + Math.random() * 6,
      delay: Math.random() * 4,
    }))
  );

  
  return (
<div
  className="raid-container"
  style={{
    backgroundImage: `url(${raidbackground})`,
    backgroundSize: "cover",
    backgroundPosition: "center",
    minHeight: "100vh"
  }}
>

<div className="headerraid">
         <a href="#/" onClick={(e) => e.stopPropagation()}>

            <img src={logo} alt="logo" className="logoimage" />
          </a>
      <h1 className="raid-title">$Paper Raid Hub</h1>
</div>




      <div className={`form-toggle ${!showForm ? "closed" : "open"}`} onClick={() => setShowForm(!showForm)}>
        ▼
      </div>

      {showForm && (
        <form className="raid-form sticky-form" onSubmit={handleSubmit}>
          <div className="formcontainer">
            <input
              type="text"
              placeholder="Paste Tweet URL"
              value={url}
              onChange={(e) => setUrl(e.target.value)}
            />
            <input
              type="text"
              placeholder="Optional Title (e.g. RAID Elon drop)"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
            />
            <button type="submit">Submit</button>
          </div>
        </form>
      )}

      <div className="raid-list">
        {Array.isArray(raids) && raids.map((raid) => (
          <div key={raid.id} className="raid-card">
            <h3>{raid.title}</h3>
            <a href={raid.url} target="_blank" rel="noopener noreferrer">
              {raid.url}
            </a>
            <span className="raid-time">{formatTimeAgo(raid.id)}</span>

            <div className="raid-actions">
<button
  className="raidbutton"
  onClick={() => {
    const tweetId = getTweetId(raid.url);
    const replyUrl = tweetId
      ? `https://twitter.com/intent/tweet?in_reply_to=${tweetId}&text=$paper`
      : raid.url;
    window.open(replyUrl, "_blank");
  }}
>
  RAID NOW
</button>

              <button onClick={() => handleShare(setCopiedShare)}>
                {copiedShare ? "Copied!" : "📣 Share"}
              </button>
              <button className="booster" onClick={() => handleBoost(raid.id)}>
                👍 Boost ({raid.boosts})
              </button>
            </div>
          </div>
        ))}
      </div>





    </div>
  );
}
